/* https://developer.helpscout.com/seed/glossary/itcss/#the-triangle */
@layer settings, tools, generic, elements, objects, typography, components, overrides;
@layer settings {
  :root {
    --clr-light: #FFDEE9;
    --clr-dark: #2C1529;
    --clr-primary: #3D8ABE;
    --clr-secondary: #402C51;
    --clr-accent: #C37236;
    --clr-orange-gr: linear-gradient(
      to right, #F7CE68, #FBAB7E
    );
    --clr-green-gr: linear-gradient(
      to right,
      #74EBD5,
      #9FACE6
    );
    --clr-purple-gr: linear-gradient(
      to right, #A9C9FF, #FFBBEC
    );
    --foreground: var(--clr-dark);
    --background: var(--clr-light);
    --gradient-top: var(--clr-purple-gr);
    --gradient-mid: var(--clr-orange-gr);
    --gradient-bottom: var(--clr-green-gr);
    --font-inter: "Inter", sans-serif;
  }

  .darkmode {
    --clr-light: #D2E1AA;
    --clr-dark: #2C1529;
    --clr-primary: #3D8ABE;
    --clr-secondary: #402C51;
    --clr-accent: #C37236;
    --clr-orange-gr: linear-gradient(
      to right, #fdc830, #f37335
    );
    --clr-green-gr: linear-gradient(
      to right,
      #00a241,
      #0575e6
    );
    --clr-purple-gr: linear-gradient(
      45deg, #6241d0, #C850C0, #C37236
    );
    --foreground: var(--clr-light);
    --background: var(--clr-dark);
    --gradient-top: var(--clr-purple-gr);
    --gradient-mid: var(--clr-orange-gr);
    --gradient-bottom: var(--clr-green-gr);
  }
}
/*
Example:

h1 {
  @include breakpoint(large) {
  font-size: 3em
  }
}

- loads breakpoint in h1 tag, 
- translates to CSS...

@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
}

*/
/**

h1 {
  font-size: rem(30)
}

results in...

h1 {
  font-size: 1.875rem
}
*/
@layer generic {
  html {
    font-size: 100%;
    box-sizing: border-box;
    scroll-behavior: smooth;
  }

  *, *::before, ::after {
    box-sizing: inherit;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--background);
    color: var(--foreground);
  }

  .wrapper {
    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;
    padding-inline: 1rem;
  }

  .section-title {
    font-size: 2.25rem;
  }

  section {
    padding-block: 5rem;
  }

  .diagonal {
    --skew-angle: -5deg;
    --background: var(--gradient-top);
    position: relative;
    isolation: isolate;
    height: 12rem;
  }

  .diagonal::after {
    content: "";
    background: var(--background);
    position: absolute;
    inset: 0;
    transform: skewY(var(--skew-angle));
    z-index: -1;
  }

  .spikes {
    --spike-color: var(--background);
    --spike-width: 80px;
    --spike-height: 12px;
    --svg-url: url("../../images/triangle.svg");
    position: relative;
    background: var(--gradient-mid);
  }

  .spikes::before,
.spikes::after {
    content: "";
    position: absolute;
    width: 100%;
    height: var(--spike-height);
    background: var(--background);
    -webkit-mask-image: var(--svg-url);
    -webkit-mask-size: var(--spike-width) var(--spike-height);
    mask-image: var(--svg-url);
    mask-size: var(--spike-width) var(--spike-height);
  }

  .spikes::before {
    top: 0;
  }

  .spikes::after {
    --svg-url: url("../../images/triangle.svg");
    --spike-width: 80px;
    --spike-height: 12px;
    bottom: 0;
    transform: rotate(0.5turn);
  }

  .wavy {
    background: var(--gradient-bottom);
    --mask:
      radial-gradient(61.77px at 50% 84px,#000 99%,#0000 101%) calc(50% - 60px) 0/120px 51% repeat-x,
      radial-gradient(61.77px at 50% -54px,#0000 99%,#000 101%) 50% 30px/120px calc(51% - 30px) repeat-x,
      radial-gradient(61.77px at 50% calc(100% - 84px),#000 99%,#0000 101%) calc(50% - 60px) 100%/120px 51% repeat-x,
      radial-gradient(61.77px at 50% calc(100% + 54px),#0000 99%,#000 101%) 50% calc(100% - 30px)/120px calc(51% - 30px) repeat-x;
    -webkit-mask: var(--mask);
    mask: var(--mask);
  }

  .background-normal {
    background: var(--background);
    z-index: 1;
  }

  .cover-up {
    background: var(--background);
    z-index: 3;
    height: 7rem;
    width: 100%;
    margin-top: -100px;
    margin-bottom: -50px;
  }

  .cover-up__bottom {
    padding-bottom: 200px;
    margin-bottom: -200px;
  }

  .cover-up__hero {
    background: var(--clr-dark);
    z-index: 3;
    height: 7rem;
    width: 100%;
    margin-bottom: -160px;
    margin-left: 0;
    margin-right: 0;
    margin-top: -150px;
  }

  .spikes__title-bg {
    background-color: rgba(223, 126, 52, 0.65);
    width: fit-content;
    padding: 1rem;
    border-radius: 0.3rem;
  }
}
@layer elements {}
@objects {}
@typography {
  :root h1,
:root h2,
:root h3 {
    margin-top: 0;
  }
  :root a,
:root a:visited,
:root a:active {
    text-decoration: none;
  }
}
@layer components {
  .card {
    padding: 1.25rem;
  }
}
@layer components {
  .navbar {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(45, 21, 38, 0.5) !important;
    color: var(--foreground);
    z-index: 2;
  }

  .navbar__title {
    font-size: 1.5rem;
    margin: 0.5rem;
  }

  .navbar__links {
    height: 100%;
  }

  .navbar__links-list {
    margin: 0;
    padding: 0;
    display: flex;
  }

  .navbar__links-item {
    list-style: none;
  }

  .navbar__link {
    text-decoration: none;
    color: white;
    padding: 1rem;
    display: block;
  }

  .navbar__link:hover {
    background-color: #555;
  }

  .navbar__toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
  }

  .navbar__toggle-button-bar {
    height: 3px;
    width: 100%;
    background-color: white;
    border-radius: 10px;
  }

  @media (max-width: 800px) {
    .navbar__toggle-button {
      display: flex;
    }

    .navbar__links {
      display: none;
      width: 100%;
    }

    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }

    .navbar__links-list {
      width: 100%;
      flex-direction: column;
    }

    .navbar__links-list .navbar__links-item {
      text-align: center;
    }

    .navbar__links-list .navbar__links-item.navbar__dark-mode-toggle {
      text-align: center;
    }

    .navbar__link {
      padding: 0.5rem 1rem;
    }

    .navbar__links.navbar__links--active {
      display: flex;
    }
  }
}
@layer components {
  .navbar__dark-mode-toggle {
    top: 1em;
    right: 1em;
    color: var(--foreground);
    border: 2px solid currentColor;
    padding: 4px;
    margin: 0.5rem;
    background: transparent;
    cursor: pointer;
    border-radius: 5px;
    color: white;
    width: 30px;
    height: 30px;
  }

  .navbar__dark-mode-toggle:hover {
    background-color: #555;
  }
}
@layer components {
  .player {
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 50;
  }

  .player__wrapper {
    border: 1px solid transparent;
    background-color: var(--background);
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
  }

  .player__details-slider {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    width: 100%;
    max-width: 100%;
    gap: 10px;
  }

  .player__details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
  }

  .player__track-name {
    font-size: 1.5rem;
  }

  .player__buttons {
    flex-shrink: 0;
    margin-right: 20px;
  }

  .active {
    color: black;
  }

  .playpause-track {
    padding: 25px;
    opacity: 0.8;
    transition: opacity 0.2s;
    background-color: var(--foreground);
  }

  .playpause-track:hover {
    opacity: 1;
  }

  .player__slider_container {
    flex-grow: 1;
    width: 100%;
  }

  .player__seek_slider, .player__volume_slider {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100% !important;
    height: 5px;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
    background: #83A9FF;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
  }

  .player__seek_slider::-webkit-slider-thumb,
.player__volume_slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background: white;
    border: 3px solid #3774FF;
    cursor: grab;
    border-radius: 100%;
  }

  .player__seek_slider:hover,
.player__volume_slider:hover {
    opacity: 1;
  }

  .player__seek_slider {
    width: 60%;
  }

  .player__current-time {
    font-size: 1rem;
  }

  i,
i.fa-play-circle,
i.fa-pause-circle,
p {
    cursor: pointer;
  }
}
@layer components {
  .full-screen-video-container {
    position: relative;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }

  .full-screen-video-container video {
    position: absolute;
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@layer components {
  .parallax {
    position: relative;
    z-index: 0;
    min-height: 75vh;
    display: grid;
    grid-template-areas: "stack";
  }

  .parallax > * {
    grid-area: stack;
    animation: parallax linear;
    animation-timeline: scroll();
  }

  .parallax > img {
    width: 100%;
  }

  .hero {
    --parallax-speed: 10;
    z-index: 10;
  }

  .parallax__bg {
    --parallax-speed: -10;
    z-index: 1;
    min-height: 100%;
    width: 100%;
    object-fit: cover;
  }

  .parallax__stars {
    --parallax-speed: -5;
    z-index: 2;
  }

  .parallax__logo {
    --parallax-speed: 15;
    z-index: 4;
    position: absolute;
    top: 30%;
    /* 50% from the top of the viewport */
    transform: translateY(-50%);
    /* Offsets the element by half its width and height to truly center it */
    height: 80rem;
    /* Set a width */
    width: auto;
    /* Maintain the aspect ratio */
    object-fit: contain;
    /* Ensure the image is not stretched */
  }

  .parallax__space-baby {
    --parallax-speed: 25;
    z-index: 5;
    position: absolute;
    top: 5%;
    left: 35%;
    transform: translateY(-25%);
    /* Offsets the element by half its width and height to truly center it */
    height: 25%;
    width: auto;
    /* Maintain the aspect ratio */
    object-fit: contain;
    /* Ensure the image is not stretched */
    max-width: 50%;
  }
  @media (max-width: 71.8125em) {
    .parallax__space-baby {
      height: 20%;
      max-width: 15rem;
      top: 10%;
      left: 45%;
    }
  }
  @media (max-width: 39.9375em) {
    .parallax__space-baby {
      height: 20%;
      max-width: 12rem;
      top: 10%;
      left: 38%;
    }
  }

  @keyframes parallax {
    to {
      transform: translateY(calc(var(--parallax-speed) * 200px));
    }
  }
  img {
    display: block;
    max-width: 100%;
  }

  .hero {
    margin-block-start: max(20vh, 8rem);
  }

  .main-content {
    position: relative;
    /* z-index: 1; */
    background: --background;
    padding-block: 6rem;
  }
}
@layer components {
  .carousel {
    width: 100%;
    height: 50vh;
    position: relative;
  }

  .carousel > ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .slide {
    position: absolute;
    inset: 0;
    opacity: 0;
    transition: 200ms opacity ease-in-out;
    transition-delay: 200ms;
  }

  .slide > img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  .slide[data-active] {
    opacity: 1;
    z-index: 1;
    transition-delay: 0ms;
  }

  .carousel-button {
    position: absolute;
    z-index: 2;
    background: none;
    border: none;
    font-size: 4rem;
    top: 50%;
    transform: translateY(-50%);
    color: var(--clr-light);
    cursor: pointer;
    border-radius: 0.25rem;
    padding: 0 0.5rem;
    background-color: var(--clr-dark);
  }

  .carousel-button:hover,
.carousel-button:focus {
    color: #eaf0db;
    background-color: #4c2847;
  }

  .carousel-button:focus {
    outline: 1px solid black;
  }

  .carousel-button.prev {
    left: 1rem;
  }

  .carousel-button.next {
    right: 1rem;
  }
}
@layer components {
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  /* Elements of Contact Form */
  .contact-form__field {
    display: flex;
    flex-direction: column;
    font-size: 1rem;
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    background-color: #f9f9f9;
    transition: border-color 0.3s, box-shadow 0.3s;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 10px, rgba(0, 0, 0, 0.22) 0px 3px 6px;
  }

  .contact-form__input {
    font-size: 1rem;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    background-color: #f9f9f9;
    transition: border-color 0.3s, box-shadow 0.3s;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 10px, rgba(0, 0, 0, 0.22) 0px 3px 6px;
  }

  .contact-form__input:focus {
    outline: none;
    border-color: #0077ff;
    box-shadow: 0 0 5px rgba(0, 119, 255, 0.3);
  }

  .contact-form__button {
    padding: 0.75rem;
    font-size: 1rem;
    max-width: 12rem;
    font-weight: bold;
    color: #fff;
    background-color: #0077ff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 10px, rgba(0, 0, 0, 0.22) 0px 3px 6px;
  }

  .contact-form__button:hover {
    background-color: #0056d6;
    transform: translateY(-2px);
  }
}
@layer components {
  .press {
    margin-bottom: 6rem;
  }

  .press_link {
    color: var(--foreground);
    text-decoration: underline;
  }

  .press__section-title {
    margin-top: 6rem;
    margin-bottom: 3rem;
  }
}
@layer components {
  .social-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }

  .social-links a {
    font-size: 30px;
    color: var(--foreground);
    text-decoration: none;
  }

  .social-links a:hover {
    color: var(--clr-accent);
  }
}
@layer components {
  .footer-text {
    display: flex;
    justify-content: center;
    z-index: 1;
    height: 8rem;
    background-color: var(--background);
  }
}
@layer components {
  .spikes__ytvideo {
    max-width: 100%;
    margin-bottom: 2rem;
  }
}
@overrides {}
@layer settings, tools, generic, elements, objects, typography, components, overrides;