
@layer components {
  .full-screen-video-container {
    position: relative;
    height: 100vh;
    width: 100vw;
    overflow: hidden;

  }

  .full-screen-video-container video {
    // z-index: -1;
    position: absolute;
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    // opacity: 15%;
  }
}